.changPage {
    padding: 10px 0;
    min-height: 95vh;
}

.change {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 60vh;
    justify-content: center;
    align-items: center;
}

.forget {
    display: flex;
    flex-direction: column;
    gap: 10px;
    height: 60vh;
    justify-content: center;
    align-items: center;
}

.title {
    text-align: center;
    margin-bottom: 10px;
    color: white;
}

.logo {
    margin: 0 auto;
    height: 10vh;
    text-align: center;
}

.logo img {
    height: 100%;
}

.changePasswordInput {
    height: 40px;
    font-size: 16px;
    width: 300px;
    padding-inline-start: 10px;
    border-radius: 10px;
    border: none;
}

.btn {
    border-radius: 10px;
    border: none;
    padding: 5px;
    height: 40px;
    font-weight: bold;
    font-size: 24px;
    cursor: pointer;
    width: 300px;
    color: black;
    background-color: rgb(238, 96, 96);
}

.btn:hover {
    background-color: rgb(233, 68, 68);
}